<template>
  <div class="article-container">
    <!-- 导航栏 -->
    <van-nav-bar class="page-nav-bar" left-arrow title="黑马头条" @click-left="$router.back()"></van-nav-bar>
    <!-- /导航栏 -->

    <div class="main-wrap">
      <!-- 加载中 -->
      <div class="loading-wrap" v-if="loading">
        <van-loading color="#3296fa" vertical>加载中</van-loading>
      </div>
      <!-- /加载中 -->

      <!-- 加载完成-文章详情 -->
      <div class="article-detail" v-else-if="article.title">
        <!-- 文章标题 -->
        <h1 class="article-title">{{article.title}}</h1>
        <!-- /文章标题 -->

        <!-- 用户信息 -->
        <van-cell class="user-info" center :border="false">
          <van-image class="avatar" slot="icon" round fit="cover" :src="article.aut_photo" />
          <div slot="title" class="user-name">{{article.aut_name}}</div>
          <div slot="label" class="publish-date">{{article.pubdate|relativeTimevalue}}</div>
          <Follow :isFollow="article.is_followed" :userId="article.aut_id"></Follow>
        </van-cell>
        <!-- /用户信息 -->

        <!-- 文章内容 -->
        <div class="article-content markdown-body" ref="article-content" v-html="article.content"></div>
        <van-divider>正文结束</van-divider>
        <!-- 底部区域 -->
        <div class="article-bottom">
          <van-button
            class="comment-btn"
            type="default"
            round
            size="small"
            @click="isPostShow=true"
          >写评论</van-button>
          <van-icon name="comment-o" :info="totalCount" color="#777" />
          <!-- 收藏 -->
          <!-- v-model 绑定数据 -->
          <Collect v-model="article.is_collected" :articleId="article.art_id"></Collect>
          <!-- 点赞 -->
          <Like v-model="article.attitude" :articleId="article.art_id"></Like>
          <van-icon name="share" color="#777777"></van-icon>
        </div>
        <!-- 评论模块 -->
        <commentList
          :list="commentList"
          :source="article.art_id"
          @onLoadSuccess="totalCount=$event.total_count"
        ></commentList>
        <!-- 评论弹出层 -->
        <van-popup v-model="isPostShow" position="bottom">
          <commentPost @postSuccess="onPostSuccess" :target="article.art_id" />
        </van-popup>
      </div>

      <!-- /加载完成-文章详情 -->

      <!-- 加载失败：404 -->
      <div class="error-wrap" v-else-if="errStatus===404">
        <van-icon name="failure" />
        <p class="text">该资源不存在或已删除！</p>
      </div>
      <!-- /加载失败：404 -->

      <!-- 加载失败：其它未知错误（例如网络原因或服务端异常） -->
      <div v-else class="error-wrap">
        <van-icon name="failure" />
        <p class="text">内容加载失败！</p>
        <van-button class="retry-btn">点击重试</van-button>
      </div>
      <!-- /加载失败：其它未知错误（例如网络原因或服务端异常） -->
    </div>
     <!-- 评论回复  弹出层数据只会加载一次 不清空那么会一直展示第一次的数据 -->
      <van-popup  v-model="isReplyShow" position="bottom"  style="height: 95%">
        <!-- v-if渲染销毁  显示弹出层渲染评论回复组件加载数据 -->
        <commentReply ref="commentReply" v-if="isReplyShow" :comment="currentComment" @close-reply="isReplyShow=false" />
      </van-popup>
  </div>
</template>

<script>
import './github-markdown.css'
import { getArticleById } from '@/api/article'
import { ImagePreview } from 'vant'
import Follow from '@/components/follow-user'
import Collect from '@/components/collectArticle'
import Like from '@/components/likeArticle'
import commentList from '@/views/article/components/comment-list'
import commentPost from '@/views/article/components/comment-post'
import commentReply from '@/views/article/components/commentreply'

export default {
  name: 'artileID',
  components: {
    [ImagePreview.Component.name]: ImagePreview.Component,
    Follow,
    Collect,
    Like,
    commentList,
    commentPost,
    commentReply
  },
  props: {
    articleId: {
      type: [Number, String, Object],
      required: true
    }
  },
  // Vue依赖注入
  // 给所有后代组件提供数据
  provide: function () {
    return {
      articleId: this.articleId
    }
  },
  data () {
    return {
      article: {}, // 文章详情
      loading: true, // 加载是否完成
      errStatus: 0, // 加载错误码
      followloading: false,
      totalCount: 0, // 评论条数
      isPostShow: false,
      commentList: [], // 内容列表
      isReplyShow: false, // 评论回复弹出层
      currentComment: {}// 点击回复的评论项
    }
  },
  created () {
    this.loadArticle()
    // 3.订阅
    this.$bus.$on('replyClick', (obj) => {
      // function this指向new Vue
      console.log(this.$refs)
      this.$refs.commentReply = {}
      this.currentComment = obj
      this.isReplyShow = true
    })
  },
  methods: {
    async loadArticle () {
      try {
        const { data } = await getArticleById(this.articleId)
        // JSON.parse('asdasdafasags')
        // if (Math.random() <= 0.9) {
        // }
        this.article = data.data
        // 初始化图片点击预览
        // 视图更新完之后执行$nextTick() update更新完之后
        setTimeout(() => {
          this.previewImage()
        }, 0)
      } catch (error) {
        if (error.response && error.response.status === 404) {
          this.errStatus = 404
        }
        this.$toast('获取数据失败')
        // this.loading = false
      }
      this.loading = false
      // this.$nextTick(() => {
      //   this.previewImage()
      // })
    },
    // 预览图片函数
    previewImage () {
      // 得到所有的img节点
      const articleContent = this.$refs['article-content']
      const imgs = articleContent.querySelectorAll('img') // 获取dom元素
      //   获取所有img地址
      const images = []
      imgs.forEach((img, index) => {
        images.push(img.src)
        // 给每个img注册点击时间 在处理函数中调用预览
        img.onclick = () => {
          ImagePreview({
            // 预览的图片地址数组
            images,
            // 起始位置 从0开始
            startPosition: index
          })
        }
      })
    },
    onPostSuccess (res) {
      // res 评论组件数据
      // 把此列表传给评论组件 评论列表组件再渲染list
      this.commentList.unshift(res.data.data.new_obj)
      this.isPostShow = false // 关闭评论弹出层
    }
  }
}
</script>

